.App {
  text-align: center;
  display: flex;
  justify-content: center;
  background-color: silver;
}

.App-logo {
  
  pointer-events: none;
  width:200px;
  height:auto;
  margin:15px;
  
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.main-app-card {
  margin: 100px;
  width: 1000px;
  
}
.child-app-paper {
  margin: 20px;
  padding:15px;
  text-align: left;
}

.required {
  color: red;
  font-weight: bold;
  font-size: larger;
}

.business-name {
  width: 600px;
  display: flex;
}

.appbar{
  position:fixed;
  display:flex;
  align-items: center;
}

.please-complete-reqd-fields{
 color: red;
}

.form-start{
  display:flex;
  height: auto;
  justify-content: space-evenly;
  align-items: center;
}

.main-input{
  width:500px;
}

hr{
  margin: 50px;
}

@media screen and (max-width: 1000px) {
  .main-app-card {
    margin: 25px;
    margin-top: 80px;
    width: 350px;
    
  }
    .main-input{
      width:250px;
    }
    .business-name {
      width: 250px;
      display: flex;
    }
    .form-start{
      display:flex;
      flex-direction: column;
      height: auto;
      justify-content: space-evenly;
      align-items: center;
    }
    
  
}
